	module.exports = {"data":{"markdownRemark":{"html":"<h1>Let My Poor Voice Be Heard</h1>\n<p>Once you become comfortable playing in front of people, the next step is to capture audience attention.\nAs with getting used to playing in front of people, the best place to do this is open mic nights.\nThe methods should be unique, as they should not tarnish your brand / your music.</p>\n<p>In general terms, you want to invoke a strong emotional response in the audience.\nThe response should be so strong, that you cannot help but listen.\nThis is why an open mic night is the perfect place to do this.\nPeople at the coffee shop / bar mostly did not come to see you.\nThey came to see their friends, or to have a coffee.</p>\n<p>Its your goal to polarize these people's emotions, and get them to listen to you.\nAny emotion is fine at the onset.\nJust get them listening.\nThen the trick is to convert those emotions into liking you.</p>\n<p>This is a two step process, and you should start by experimenting at the polarization.\nIf you are not failing at it, then you are not polarizing enough.\nOnce you can polarize, then start practicing your conversion.</p>\n<p>Its more important to polarize than for them to like you.\nYou can always convert a hater later.</p>\n<h1>All Eyes On Me</h1>\n<p>If you are playing regularly, the best way to make this switch is by adopting the mantra <em>all eyes on me</em>.\nIf you look out into the crowd, and people are not looking at you then you failed.\nYou need to polarize more.\nIterate over that.</p>","frontmatter":{"title":"Thoughts On Audience Attention"},"fields":{"date":"2016-02-23"}}},"pathContext":{"url":"/posts/2016-02-23-live-performing-skills/"}}

